@media screen and (max-width: 767.8px) {
  .header .navigation {
    position: relative;
  }

  .header .wrap {
    width: 130px;
    pointer-events: none;
    opacity: 0;
    background: #fefefe;
    border-radius: 30px 0 30px 30px;
    padding: 0;
    transition: all .25s;
    position: absolute;
    top: 40px;
    right: 20px;
    overflow: hidden;
    transform: translateY(-50%)scaleY(.1);
    box-shadow: 0 4px 24px rgba(105, 105, 105, .1);
  }

  .header .wrap.wrap-mobile {
    pointer-events: all;
    opacity: 1;
    transform: translateY(0)scaleY(1);
  }

  .navigation__link {
    padding: 16px;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.19;
    transition: background-color .25s cubic-bezier(0, .55, 1, .37);
    display: block;
  }

  .navigation__link:hover, .navigation__link:focus, .navigation__link--active {
    background-color: #bbf330;
  }

  .header .close {
    background: none;
    border: none;
    position: absolute;
    top: 4px;
    right: 4px;
  }

  .header .close:hover, .header .close:focus {
    cursor: pointer;
  }

  .header .btn {
    display: none;
  }

  .header .burger {
    fill: currentColor;
    color: #7eb101;
    background: none;
    border: none;
    transition: color .25s cubic-bezier(0, .55, 1, .37);
    display: inline-block;
  }

  .header .burger:hover, .header .burger:focus {
    cursor: pointer;
    color: #ffe550;
  }
}

@media screen and (min-width: 767.8px) {
  .close, .burger {
    display: none;
  }

  .header .wrap {
    align-items: center;
    margin-left: auto;
    display: flex;
  }

  .header .navigation__list {
    gap: 28px;
    margin-right: 30px;
    display: flex;
  }

  .header .navigation__link {
    padding: 20px 0;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.14;
    transition: all .25s cubic-bezier(0, .55, 1, .37);
  }

  .header .navigation__link:hover, .header .navigation__link:focus, .header .navigation__link--active {
    color: #bbf330;
    text-decoration: underline;
  }

  .header .btn {
    text-align: center;
    background-color: #bbf330;
    border: none;
    border-radius: 20px;
    margin: 0 auto;
    padding: 6px 18px;
    font-size: 13px;
    font-weight: 500;
    line-height: 1.5;
    transition: background-color .25s cubic-bezier(0, .55, 1, .37);
    display: flex;
  }

  .header .btn:hover, .header .btnfocus {
    cursor: pointer;
    background-color: #b5ff00;
  }
}

