@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
@import './mixins';

body {
  font-family: Roboto;
  @include fSize(400, 14, 150%);
  color: $primary-text-color;
  @media screen and (min-width: $tablet) {
    @include fSize(400, 16px, 150%);
  }
}

.container {
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  @media screen and (min-width: $mobile) {
    width: 480px;
  }
  @media screen and (min-width: $tablet) {
    padding-right: 32px;
    padding-left: 32px;
    width: 768px;
  }
  @media screen and (min-width: $desktop) {
    padding-left: 16px;
    padding-right: 16px;
    width: 1280px;
  }
}

.section__title {
  @include fSize(700, 28px, 1.21);
  color: $primary-text-color;
  @media screen and (min-width: $tablet) {
    @include fSize(700, 42px, 1.17);
  }
}

// raccoons normalize

html {
  box-sizing: border-box;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

address {
  font-style: normal;
}

button {
  font-family: inherit;
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
